<template>
	<div>
		<ul class="croplist" v-if="cropList.length > 0">
			<li class="cropitem" v-for="crop in cropList.slice(0, 1)" :key="crop.id">
        <div class="pic-wrap">
          <img v-if="crop.imgUrl" :src="crop.imgUrl | aliimg(90,90,true)" alt="" />
        </div>
				<div class="cropdes">
					<p class="cropname">
            <span class="breed">{{crop.name}}</span>
          </p>
          <p class="output">
            作物种类：{{crop.type }}
          </p>
					<p class="time">
            <span>{{crop.endDate | znDateFormat('yyyy.MM.dd')}}{{crop.plantway}}</span>
          </p>
				</div>
			</li>
		</ul>
		<div class="nodata" v-if="cropList.length === 0">
			<img src="~@/assets/images/wuzuowu.png" alt="无数据" />
			<p>暂无种养品种</p>
		</div>
	</div>
</template>

<script>
// import { znDateFormat, differenceDay } from '@/common/fn/index';
export default {
  data () {
    return {
    };
  },
  props: {
    cropList: {
      type: Array,
      default () {
        return [];
      }
    }
  },
  methods: {
  },
  computed(){

  },
  filters: {
  }
};
</script>

<style scoped lang="less">
  .flex(@x:center,@y:center){
    display:flex;
    display:-webkit-flex;
    flex-flow:row nowrap;
    justify-content:@x;
    align-items:@y;
  }
  .fontS(@s:14px){
    font-size:@s;
  }
  .textAlign(@align:center){
    text-align:@align;
  }
  .croplist{
    .fontS();
    .cropitem{
      background:#061F36;
      padding:15px;
      color:#6DC1CB;
      margin-bottom:20px;
      .cropdes{
        width: 100%;
      }
      .textAlign(left);
      .flex(flex-start, flex-start);
      .time{
        span{
          &:nth-child(1){
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            vertical-align: middle;
          }
        }
      }
      .pic-wrap{
        border-radius:50%;
        width:90px;
        height:90px;
        min-width: 90px;
        max-width: 90px;
        margin-right:30px;
        overflow: hidden;
        background: url("~@/assets/images/overview/crop-no-pic-bg.png")no-repeat center center;
        background-size: 46px 40px;
        background-color: #0C5872;
        img{
          border: 0 none;
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .cropname{
        color:#A1EFF8;
        line-height:30px;
        width: 360px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        .fontS(16px);
        .croplevel{
          background-color: #02B8FF;
          .fontS(14px);
          color:#fff;
          line-height:22px;
          padding:0 10px;
          border-radius:11px;
          margin-left:10px;
        }
      }
      .output{
        line-height:26px;
        margin-top: 5px;
      }
      .time{
        line-height:26px;
        margin-top: 5px;
      }
    }
    .gray.cropitem{
      .breed,.output,.time{
        color:rgba(161,239,248,0.41);
      }
      &>img{
        opacity:0.6;
      }
    }
  }
  .nodata{
    height:460px;
    .flex();
    flex-flow:column;
    p{
      color:#6DC1CB;
      .fontS(20px);
      line-height:42px;
      margin-bottom: 30px;
    }
  }

  .loading /deep/ .el-loading-mask{background-color:rgba(0,0,0,0);padding-top:200px;}
</style>



